import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Link from './Link';

const uuidv4 = require('uuid/v4');

class Links extends Component {

  constructor() {
    super();
    this.counter = 0; //Counter used to count links (Determine colors)
  }

  getRowStyle = () => {
    return {
      display: 'flex',
      flexWrap: 'wrap'
    }
  }

  loopFunction = (linkObject) => {
    this.counter++;
    if (this.counter === 5) {
      this.counter = 1;
    }
    return <Link key={uuidv4()} showName = {linkObject.showName}
      link ={linkObject.link} season = {linkObject.season}
      episode = {linkObject.episode} site = {linkObject.site}
      linkNo = {this.counter} />;
    }

  render() {
    this.counter = 0; //Reset counter everytime render is called

    return (<div style = {this.getRowStyle()}>
          {this.props.links.map(this.loopFunction)}
        </div>);
  }
}

Links.propTypes = {
  links: PropTypes.array.isRequired,
}

export default Links;
