import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Link extends Component {

  getLinkStyle = () => {
    return {
      color: "#fff"
    };
  }

  getDivStyle = () => {

    let bg = "#707070"; //Default background is gray

    //If linkNo is 3 or 4 it means they should be different colored, aka black
    if (this.props.linkNo === 3 || this.props.linkNo === 4) {
      bg = "#282828";
    }

    return {
      background: bg,
      flex: '50%',
      padding: '10px'
    };
  }

  render() {
    return ( <div style = {this.getDivStyle()}>
      <h4><a style={this.getLinkStyle()} href={this.props.link} rel='noopener noreferrer' target='_blank'>{this.props.site}:
       &nbsp;{this.props.showName} Season {this.props.season} Episode {this.props.episode}</a></h4>
    </div> );
  }
}

Link.propTypes = {
  link: PropTypes.string.isRequired,
  showName: PropTypes.string.isRequired,
  season: PropTypes.string.isRequired,
  episode: PropTypes.string.isRequired,
  site: PropTypes.string.isRequired,
  linkNo: PropTypes.number.isRequired
}

export default Link;
