import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'

const link_1337x = "https://1337x.to/search/";
const link_1337x_end = "/1/";

const link_tpb = "https://thepiratebay.org/search/";
const link_tpb_end = "/0/99/0";

const tpb = "Thepiratebay";
const t1337x = "1337x";

class GenerationForm extends Component {
  state = {
    showName: '',
    seasonNo: '',
    noOfEps: '',
    sites: [tpb, t1337x]
  }

  onChange = (e) => this.setState({ [e.target.name]: e.target.value});

  onChecked = (e) => {
    if (e.target.checked) {
      this.state.sites.push(e.target.name);
    }
    else {
      this.state.sites.splice( this.state.sites.indexOf(e.target.name),1);
    }
  }



  createLink = (site, showName, seasonNo, currentEp) => {
    if (seasonNo.length<2) {
      seasonNo = "0"+seasonNo;
    }
    seasonNo = "s"+seasonNo;

    if (currentEp<10) {
      currentEp = "0"+currentEp;
    }
    currentEp = "e"+currentEp;

    if (site === tpb) {
      showName = showName.replace(" ", "%20");

      return link_tpb+showName+"%20"+seasonNo+currentEp+link_tpb_end;
    }

    showName = showName.replace(" ", "+");

    return link_1337x+showName+"+"+seasonNo+currentEp+link_1337x_end;
  }

  onSubmit = (e) => {
    e.preventDefault();
    let linksArray = []
    for(let i = 1; i<=this.state.noOfEps; i++) {
      if (this.state.sites.includes(t1337x)) {
        linksArray.push({
          link: this.createLink(t1337x, this.state.showName, this.state.seasonNo, i),
          site: t1337x,
          showName: this.state.showName,
          season: this.state.seasonNo,
          episode: String(i)});
      }

      if (this.state.sites.includes(tpb)) {
        linksArray.push({
          link: this.createLink(tpb, this.state.showName, this.state.seasonNo, i),
          site: tpb,
          showName: this.state.showName,
          season: this.state.seasonNo,
          episode: String(i)});
      }
    }

    this.props.setLinks(linksArray);

  }

  render() {
    return (
      <Form onSubmit={this.onSubmit} style={{display: 'flex', marginTop: '5px'}}>
        <Form.Control
          type = "text"
          required
          name = "showName"
          style = {{flex: '5', marginLeft: '5px', marginRight: '5px'}}
          placeholder = "Name of the TV Show"
          value = {this.state.showName}
          onChange = {this.onChange} />
        <Form.Control
          required
          type = "number"
          name = "seasonNo"
          style = {{flex: '2', marginRight: '5px'}}
          placeholder = "Season number"
          value = {this.state.seasonNo}
          onChange = {this.onChange} />
        <Form.Control
          required
          type = "number"
          name = "noOfEps"
          style = {{flex: '2', marginRight: '5px'}}
          placeholder = "Episodes in season"
          value = {this.state.noOfEps}
          onChange = {this.onChange} />

        <Form.Group style={{flex: '1', marginRight: '1px'}}>
          <Form.Check
            defaultChecked = "true"
            type="checkbox"
            label = {tpb}
            name = {tpb}
            onChange = {this.onChecked}
            />
          <Form.Check
            defaultChecked = "true"
            type="checkbox"
            label = {t1337x}
            name = {t1337x}
            onChange = {this.onChecked}
            />
        </Form.Group>

        <Button
          variant="info"
          type="submit"
          style = {{flex: '1.5', marginRight: '5px', height: '45px'}}>
          Generate
        </Button>

      </Form>
    );
  }
}

GenerationForm.propTypes = {
  setLinks: PropTypes.func.isRequired,
}

export default GenerationForm;
